@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;1,700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-family: 'Roboto', sans-serif;
  line-height: 1;
  background-color: #222;
}

@media only screen and (max-width: 62.5em) {
  html {
    font-size: 50%;
  }
}

@media only screen and (max-width: 37.5em) {
  body {
    background-color: #e6f4f0;
  }
}

/* --- 01 TYPOGRAPHY SYSTEM
- Font sizes (px)
10 / 12 / 14 / 16 / 18 / 20 / 24 / 30 / 36 / 44 / 52 / 62 / 74 / 86 / 98 */

/* - Spacing system (px)
2 / 4 / 8 / 12 / 16 / 24 / 32 / 48 / 64 / 80 / 96 / 128 */

.AddContacts_input-box__3fhM4 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  grid-gap: 0.8rem;
  gap: 0.8rem;
}

.AddContacts_input-box__3fhM4:not(:last-of-type) {
  margin-bottom: 3.2rem;
}

.AddContacts_input-box__3fhM4 label {
  font-size: 2rem;
  font-weight: 500;
  color: #222;
}

.AddContacts_input-box__3fhM4 input {
  font-size: 2rem;
  padding: 0.7rem 1.2rem;
  width: 70%;
  border: 1px solid #c6c6c6;
  outline: none;
  border-radius: 7px;
}

.AddContacts_btn-box__2ckux {
  display: flex;
  align-items: center;
  margin-top: 4.8rem;
  grid-gap: 4.8rem;
  gap: 4.8rem;
}

.AddContacts_btn__37CBs {
  padding: 1.4rem 4.8rem;
  background: none;
  border: none;
  background-color: #099268;
  color: #fff;
  font-size: 2rem;
  font-weight: 700;
  border-radius: 7px;
  transition: all 0.3s;
  text-transform: capitalize;
}

.AddContacts_btn__37CBs:hover {
  background-color: #229d77;
  cursor: pointer;
}

.AddContacts_manage-text__aGoYP {
  font-size: 1.8rem;
  font-weight: 500;
  color: #222;
}

@media only screen and (max-width: 50em) {
  .AddContacts_input-box__3fhM4 input {
    width: 83%;
  }

  .AddContacts_btn-box__2ckux {
    flex-direction: column;
    align-items: flex-start;
    grid-gap: 3.2rem;
    gap: 3.2rem;
    margin-top: 3.2rem;
  }
}

@media only screen and (max-width: 37.5em) {
  .AddContacts_input-box__3fhM4 input {
    width: 100%;
  }

  .AddContacts_btn__37CBs {
    width: 100%;
  }

  .AddContacts_btn-box__2ckux {
    align-items: center;
    margin-top: 4.8rem;
    text-align: center;
  }

  .AddContacts_manage-text__aGoYP {
    font-size: 2rem;
  }
}

.Contacts_details-box__2UV0E {
  display: flex;
  flex-direction: column;
  grid-gap: 3px;
  gap: 3px;
  margin-bottom: 4.8rem;
}

.Contacts_name__25ers {
  font-size: 1.8rem;
  font-weight: 500;
}

.Contacts_phone__CuE1Y,
.Contacts_email__2n7ty,
.Contacts_delete__1_wuN {
  font-size: 1.6rem;
}

.Contacts_delete__1_wuN {
  color: #099268;
  text-decoration: underline;
  cursor: pointer;
  transition: all 0.3s;
}

.Contacts_delete__1_wuN:hover {
  color: #229d77;
}

@media only screen and (max-width: 37.5em) {
  .Contacts_details-box__2UV0E {
    grid-gap: 4px;
    gap: 4px;
  }

  .Contacts_name__25ers {
    font-size: 2rem;
  }

  .Contacts_phone__CuE1Y,
  .Contacts_email__2n7ty,
  .Contacts_delete__1_wuN {
    font-size: 1.8rem;
  }
}

.main-container {
  max-width: 120rem;
  margin: 15rem auto;
  padding: 0 4.8rem;
}

.container {
  border-radius: 10px;
  overflow: hidden;
  /* border: 1px solid #099268; */
}

.header {
  background-color: #099268;
  padding: 2.4rem 0 2.4rem 4rem;
}

.heading {
  font-size: 3.6rem;
  color: #fff;
  letter-spacing: 0.1rem;
  font-weight: 700;
}

.contact-box {
  display: grid;
  grid-template-columns: 65fr 35fr;
  grid-template-rows: 40rem;
  padding: 4.8rem 4.8rem 4.8rem 7rem;
  background-color: #e6f4f0;
}

.contact-form {
  border-right: 2px solid #dad9d9;
  padding-top: 2.4rem;
}

.contact-list {
  padding: 3.2rem 2.4rem 3.2rem 4.8rem;
  overflow-x: auto;
}

@media only screen and (max-width: 50em) {
  .main-container {
    padding: 0 3.2rem;
  }

  .contact-box {
    padding: 3.2rem 4.8rem 4.8rem 5.2rem;
    grid-template-columns: 60fr 40fr;
  }

  .contact-form {
    padding-top: 1.2rem;
  }

  .contact-list {
    padding: 3.2rem 2.4rem 3.2rem 3.2rem;
    overflow-x: auto;
  }
}

@media only screen and (max-width: 37.5em) {
  .main-container {
    padding: 0;
    margin: 0 auto;
  }

  .container {
    border-radius: 0;
  }

  .contact-box {
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    padding: 3.2rem 4.8rem 4.8rem 4.8rem;
  }

  .contact-form {
    border-right: none;
  }

  .contact-list {
    padding: 6.4rem 0;
    overflow: auto;
    text-align: center;
    justify-self: center;
    overflow: visible;
  }
}

